<template>
  <Popup v-model="isVisible" :buttons="popupButtons">
    <div class="flex flex-col gap-0.5">
      <h3></h3>
      <div class="flex flex-col gap-0.5">
        <label>
          <strong></strong><small class="block"></small>
          <input v-model="title" type="text" class="text_pole" placeholder="" />
        </label>
        <label>
          <strong></strong>
          <input v-model="url" type="text" class="text_pole" placeholder="" required />
        </label>
      </div>
    </div>
  </Popup>
</template>

<script setup lang="ts">
import { handle_url_to_title } from '@/function/audio';

const props = defineProps<{
  item: { title: string; url: string };
  onSubmit?: (item: { title: string; url: string }) => void;
}>();

const url = ref(props.item.url);
const title = ref(props.item.title || '');

const popupButtons = computed(() => [
  {
    name: '',
    shouldEmphasize: true,
    onClick: submit,
  },
  { name: '' },
]);

const isVisible = ref(true);

const submit = (close: () => void) => {
  if (!url.value.trim()) {
    return;
  }

  //  URL 
  const finalTitle = title.value.trim() || handle_url_to_title(url.value);

  props.onSubmit?.({
    url: url.value,
    title: finalTitle,
  });
  close();
};
</script>
