<template>
  <Popup :buttons="[{ name: t`` }]">
    <div class="my-1.25 flex flex-col flex-wrap gap-0.5">
      <!-- TODO:  -->
      <div>
        {{ t`, ` }}<br />
        {{ t`, ` }}
        <a
          href="https://n0vi028.github.io/JS-Slash-Runner-Doc/guide//.html"
          target="_blank"
        >
          {{ t`` }}
        </a>
      </div>
      <!-- TODO:  ScriptItem  -->
      <!-- prettier-ignore-attribute -->
      <div
        v-for="builtin in builtins"
        :key="builtin.name"
        class="flex w-full items-center justify-between rounded-sm border border-(--SmartThemeBorderColor) p-0.25"
      >
        <div class="ml-0.5 flex-grow overflow-hidden text-left! text-ellipsis! whitespace-nowrap!">
          {{ builtin.name }}
        </div>
        <div class="flex flex-shrink-0! flex-nowrap! items-center gap-0.5">
          <DefineToolButton v-slot="{ name, icon }">
            <div class="menu_button interactable my-0! mr-0.5" :title="name">
              <i class="fa-solid" :class="icon"></i>
            </div>
          </DefineToolButton>
          <ToolButton name="" icon="fa-info-circle" @click="openInfo(builtin)" />
          <ToolButton name="" icon="fa-plus" @click="importScript(builtin)" />
        </div>
      </div>
    </div>
  </Popup>
</template>

<script setup lang="ts">
import Popup from '@/panel/component/Popup.vue';
import { useGlobalScriptsStore } from '@/store/scripts';
import { Script } from '@/type/scripts';
import { uuidv4 } from '@sillytavern/scripts/utils';
import { marked } from 'marked';

const [DefineToolButton, ToolButton] = createReusableTemplate<{
  name: string;
  icon: string;
}>();

type BuiltinScript = {
  name: string;
  content_url: string;
  info_url: string;
};
async function getInfo(script: BuiltinScript): Promise<string> {
  const response = await fetch(script.info_url);
  if (!response.ok) {
    throw Error(`: ${response.status} ${response.statusText}`);
  }
  return response.text();
}
async function toScript(script: BuiltinScript): Promise<Script> {
  return {
    type: 'script',
    enabled: false,
    name: script.name,
    id: uuidv4(),
    content: `import '${script.content_url}'`,
    info: await getInfo(script),
    button: {
      enabled: true,
      buttons: [],
    },
    data: {},
  };
}

const builtins: BuiltinScript[] = [
  {
    name: t`: `,
    content_url: 'https://testingcf.jsdelivr.net/gh/StageDog/tavern_resource/dist///index.js',
    info_url: 'https://testingcf.jsdelivr.net/gh/StageDog/tavern_resource/src///README.md',
  },
  {
    name: t``,
    content_url: 'https://testingcf.jsdelivr.net/gh/StageDog/tavern_resource/dist///index.js',
    info_url: 'https://testingcf.jsdelivr.net/gh/StageDog/tavern_resource/src///README.md',
  },
  {
    name: t``,
    content_url:
      'https://testingcf.jsdelivr.net/gh/StageDog/tavern_resource/dist///index.js',
    info_url: 'https://testingcf.jsdelivr.net/gh/StageDog/tavern_resource/src///README.md',
  },
  {
    name: t``,
    content_url:
      'https://testingcf.jsdelivr.net/gh/StageDog/tavern_resource/dist///index.js',
    info_url:
      'https://testingcf.jsdelivr.net/gh/StageDog/tavern_resource/src///README.md',
  },
  {
    name: t``,
    content_url:
      'https://testingcf.jsdelivr.net/gh/StageDog/tavern_resource/dist///index.js',
    info_url: 'https://testingcf.jsdelivr.net/gh/StageDog/tavern_resource/src///README.md',
  },
  {
    name: t`: `,
    content_url: 'https://testingcf.jsdelivr.net/gh/StageDog/tavern_resource/dist///index.js',
    info_url: 'https://testingcf.jsdelivr.net/gh/StageDog/tavern_resource/src///README.md',
  },
  {
    name: t``,
    content_url: 'https://testingcf.jsdelivr.net/gh/StageDog/tavern_resource/dist///index.js',
    info_url: 'https://testingcf.jsdelivr.net/gh/StageDog/tavern_resource/src///README.md',
  },
  {
    name: t`:  AI `,
    content_url: 'https://testingcf.jsdelivr.net/gh/StageDog/tavern_resource/dist///index.js',
    info_url: 'https://testingcf.jsdelivr.net/gh/StageDog/tavern_resource/src///README.md',
  },
  {
    name: t`token: AI`,
    content_url: 'https://testingcf.jsdelivr.net/gh/StageDog/tavern_resource/dist//token/index.js',
    info_url: 'https://testingcf.jsdelivr.net/gh/StageDog/tavern_resource/src//token/README.md',
  },
  {
    name: t``,
    content_url: 'https://testingcf.jsdelivr.net/gh/StageDog/tavern_resource/dist///index.js',
    info_url: 'https://testingcf.jsdelivr.net/gh/StageDog/tavern_resource/src///README.md',
  },
];

async function openInfo(builtin: BuiltinScript) {
  toastr.info(t`...`);
  useModal({
    component: Popup,
    attrs: {
      buttons: [{ name: t`` }],
      onOpened: () => {
        toastr.clear();
      },
    },
    slots: {
      default: `<div class="p-1.5 text-left">${marked.parse(await getInfo(builtin))}</div>`,
    },
  }).open();
}

async function importScript(builtin: BuiltinScript) {
  toastr.info(t`...`);
  const script = await toScript(builtin);
  toastr.clear();
  useGlobalScriptsStore().script_trees.push(script);
  toastr.success(t`: '${script.name}'`);
}
</script>
